import { DatasetScope } from "@rp/common/api-types"
import { GenericModalWrapper } from "@rp/react/web/common/composite-components"
import { ScopeSelectorModalWrapper } from "./ScopeSelectorModalWrapper"

type Props = {
  isOpen: boolean
  handleClose: () => void
  initialDataset?: DatasetScope
  setScope: (scope: DatasetScope) => void
  enableFreeTier: boolean
  sourceFilter: Partial<Record<string, boolean>>
}

const ScopeSelectorModal = ({
  isOpen,
  handleClose,
  setScope,
  enableFreeTier,
  initialDataset,
  sourceFilter,
}: Props) => {
  return (
    <GenericModalWrapper isOpen={isOpen} handleClose={handleClose}>
      <ScopeSelectorModalWrapper
        handleClose={handleClose}
        selectScope={setScope}
        enableFreeTier={enableFreeTier}
        initialScope={initialDataset}
        sourceFilter={sourceFilter}
      />
    </GenericModalWrapper>
  )
}

export { ScopeSelectorModal }
